import React, { Component } from "react";
import { Layout, Menu, Icon, Avatar, Dropdown, Spin } from "antd";
import { fromJs, Map, Set } from "immutable";
import PropTypes from "prop-types";
import cls from "classnames";
import { StyleContext } from "../context/style-context";
const { Header, Content, Sider } = Layout;

class HdLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  renderRightMenu = () => {
    return (
      <Menu>
        <Menu.Item key="password">
          <Icon type="setting" />
          <span>修改密码</span>
        </Menu.Item>
        <Menu.Item key="signout">
          <Icon type="logout" />
          <span>退出登录</span>
        </Menu.Item>
      </Menu>
    );
  };
  render() {
    let styleX = this.context;
    console.log("styleX.prefixCls", styleX);

    const { prefixCls, className, style } = this.props;
    const { children, initData, menus, ...atr } = this.props;

    const btnClass = cls(prefixCls, className, {
      "btn-pressed": this.state.isPressed,
      "btn-over": !this.state.isPressed && this.state.isHovered
    });
    return (
      <Layout className="hd-layout-container">
        <Sider theme="light" trigger={null} collapsible width={218} breakpoint="lg" className="layout-sider">
          <div className="logo">
            {initData.logos}
            <span>{initData.title}</span>
          </div>
          {menus}
        </Sider>
        <Layout>
          <Header className="layout-header">
            <div>
              <Icon className="trigger" />
              <span>{initData.subTitle}</span>
            </div>
            <div>
              <Dropdown overlay={this.renderRightMenu} placement="bottomRight">
                <div>
                  <Avatar size="default" icon="user" />
                  <span>用户</span>
                </div>
              </Dropdown>
            </div>
          </Header>
          <Content />
        </Layout>
      </Layout>
    );
  }
}

HdLayout.contextType = StyleContext;

export default HdLayout;
